.MainClass {
    padding: 10px;
    background-color: rgb(47, 255, 175);
    width:100%;
    height: 2000px;
    margin: auto;
    font-size: medium;
}

h2, h3 {
    text-align: center;
    font-size: x-large;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.ContentContainer {
    display: inline-block;
    float: left;
    width: 45%;
    height: 50%;
    text-align: center;
    padding: 5px;
    margin: auto;
}

.PreviewImage {
    width: 50%;
    text-align: center;
}

textarea {
    background-color: white;
    height: 50%;
    font-size: x-large;
}

label {
    font-size: larger;
    font-weight: bolder;
}

@media only screen and (max-width: 600px) {
    .MainClass {
        background-color: rgb(47, 255, 175);
        width:100%;
        height: 2000px;
        margin: auto;
    }

    .ContentContainer {
        display: block;
        float: none;
        width: 100%;
         height: 20%;
    }

    .PreviewImage {
        width: 40%;
        text-align: center;
    }

    textarea {
        background-color: white;
        width: 80%;
        height: 20%;
        font-size: small;
    }

    label {
        font-size:medium;
        font-weight: bolder;
    }
}